@charset "UTF-8";
/**
 * Default Setting
 */
html {
  height: 100%; }

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 0.92em;
  color: #3c3c3c;
  background-color: #0093e9;
  background-image: linear-gradient(-80deg, #BCCFEB 0%, #3488C9 100%);
  background-size: cover; }

a:hover {
  text-decoration: none; }

*,
*:before,
*:after {
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-size: 100%; }

a {
  color: #79a1d4; }

input[type=text],
input[type=password],
input[type=email] {
  appearance: none;
  display: block;
  width: 100%;
  height: 40px;
  margin-top: 5px;
  padding: 0 12px;
  font-size: 16px;
  color: #454545;
  letter-spacing: 1px;
  background-color: rgba(255, 255, 255, 0.2);
  outline: 0;
  border: 1px solid rgba(255, 255, 255, 0.4);
  transition: 235ms ease; }
  input[type=text]:focus,
  input[type=password]:focus,
  input[type=email]:focus {
    border-color: #ffeed5; }
  input[type=text]:not([value=""]),
  input[type=password]:not([value=""]),
  input[type=email]:not([value=""]) {
    border: 1px solid #454545; }

input[type=checkbox] {
  appearance: none; }

#login {
  max-width: 400px;
  margin: 60px auto; }
  #login h1 {
    margin-bottom: 32px;
    text-align: center; }
    #login h1 a {
      font-size: 24px;
      color: #454545;
      letter-spacing: 1px;
      text-decoration: none; }
    #login h1 img {
      max-width: 100%;
      height: auto; }

form {
  max-width: 400px;
  min-width: 300px;
  width: 100%;
  padding: 26px;
  padding: 0 0 26px;
  background-color: rgba(255, 255, 255, 0); }
  form:after {
    content: "";
    display: block;
    height: 1px;
    clear: both; }
  form label {
    font-size: 13px;
    color: #454545; }
  form p {
    margin: 0 0 1em; }
    form p:last-child {
      margin-bottom: 0; }

.forgetmenot {
  margin-top: 1.6em; }
  .forgetmenot label {
    cursor: pointer;
    position: relative;
    padding-left: 2.4em;
    user-select: none; }
  .forgetmenot input[type=checkbox] {
    position: absolute;
    display: block;
    width: 16px;
    height: 16px;
    margin-right: 8px;
    outline: 0;
    background-color: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.4);
    transition: 235ms ease; }
    .forgetmenot input[type=checkbox]:before {
      content: "";
      position: absolute;
      top: 2px;
      left: 2px;
      display: block;
      width: 10px;
      height: 6px;
      opacity: 0;
      border-left: 2px solid #79a1d4;
      border-bottom: 2px solid #79a1d4;
      transform: rotate(-45deg);
      transition: opacity 235ms ease; }
    .forgetmenot input[type=checkbox]:focus {
      box-shadow: 0 0 2px 1px #ffeed5; }
    .forgetmenot input[type=checkbox]:checked {
      background-color: #ffeed5;
      border-color: #ffe4bc; }
      .forgetmenot input[type=checkbox]:checked:before {
        opacity: 1; }

.submit {
  margin-top: 32px;
  margin-bottom: 0 !important; }

.button {
  padding: 6px 10px;
  line-height: 1;
  font-size: 15px;
  color: #666;
  letter-spacing: 2px;
  text-decoration: none;
  background-color: #eee;
  border: 1px solid #ddd; }
  .button:hover {
    opacity: 0.8; }

.button-large {
  padding: 8px 12px; }

.button-primary {
  appearance: none;
  cursor: pointer;
  display: block;
  max-width: 200px;
  width: 100%;
  margin: 0 auto;
  color: #fff;
  background-color: #0093e9;
  border: 1px solid #0083d0; }

#nav {
  padding: 0 0;
  font-size: 12px;
  text-align: right; }
  #nav a {
    color: #fff; }

#backtoblog {
  display: none; }

#login_error {
  margin-bottom: 2em;
  padding: 10px 14px;
  font-size: 14px;
  background-color: #fff;
  box-shadow: 0 3px 8px -1px rgba(0, 0, 0, 0.1);
  border-left: 5px solid #e53; }

.wp-pwd {
  position: relative; }
  .wp-pwd input[name=pwd] {
    padding-right: 36px; }
  .wp-pwd .button {
    cursor: pointer;
    position: absolute;
    top: 1px;
    right: 1px;
    bottom: 1px;
    display: block;
    width: 36px;
    padding: 0;
    background-color: transparent;
    border: 0;
    outline: 0;
    border-radius: 0; }
    .wp-pwd .button:focus {
      outline: 1px solid rgba(69, 69, 69, 0.4); }
    .wp-pwd .button:hover .dashicons-visibility {
      color: #454545; }
    .wp-pwd .button .dashicons-visibility {
      color: rgba(69, 69, 69, 0.6); }
    .wp-pwd .button .dashicons-hidden {
      color: #454545; }

.message {
  min-width: 240px;
  padding: 12px 16px;
  font-family: "游ゴシック", YuGothic, "游ゴシック体", "Yu Gothic", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", "MS PGothic", Robot, "Droid Sans", sans-serif;
  font-size: 14px;
  color: #155724;
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 6px;
  box-shadow: 3px 3px 10px 0 rgba(0, 0, 0, 0.1); }
  @media (min-width: 641px) {
    .message {
      position: fixed;
      top: 20px;
      right: 16px;
      z-index: 10;
      margin: 0; } }

.admin-email__actions-primary {
  display: flex;
  justify-content: space-between; }

.admin-email__actions-secondary {
  margin: 2em 0 0;
  text-align: right; }
